@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700;800&display=swap);
* {
  font-family: "Open Sans", sans-serif;
}
/* Hide scrollbar for Chrome, Safari and Opera */
html::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
html {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
  scroll-behavior: smooth;
  background-color: rgb(235, 238, 255);
}
a {
  text-decoration: none;
}
input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0) !important;
}

.banner {
  height: 750px;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
}
#landing-body {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(255, 215, 74);
}
.banner-heading {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.website-name {
  text-align: center;
  font-family: "Crimson Text", serif !important;
  color: rgb(59, 39, 17) !important;
  font-size: 10vw;
  margin: 0;
  padding: 0;
  display: inline-block;
}
.sub-heading {
  text-align: center;
  margin-top: 0;
  font-size: 1.5rem;
  color: rgb(59, 39, 17) !important;
  margin: 0;
  margin-top: -7px;
  padding: 0;
  font-weight: 600;
}
.search-options {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 120px;
  margin-top: 50px;
}
.option {
  background-color: rgb(59, 39, 17);
  color: rgb(255, 215, 74);
  border-radius: 10px;
  width: 450px;
  box-shadow: 0 -2px 10px rgba(0, 0, 0, 1);
  display: inline-block;
}
.landing-images {
  width: 100%;
  display: block;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
}
.option-heading {
  text-transform: uppercase;
  text-align: center;
  padding: 0;
  font-size: 1.5rem;
  font-weight: 700;
  padding: 10px 0;
}

@media screen and (max-width: 1100px) {
  .option {
    width: 85%;
    margin: 0;
    margin-bottom: 30px;
  }
  .website-name {
    font-size: 20vw;
  }
  .sub-heading {
    font-size: 4.5vw;
  }
  .search-options {
    width: 100%;
    margin: 0;
    margin-top: 40px;
    align-items: center;
  }
  a {
    display: flex;
    justify-content: center;
  }
  html {
    padding: 0;
  }
  .banner {
    height: 90vh;
  }
}
@media screen and (max-height: 800px) {
  .banner {
    height: 100vh;
  }
}

#content-box {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  background-color: rgb(235, 238, 255);
  margin-left: -8px;
  margin-right: -8px;
  padding: 20px;
  padding-top: 0;
  z-index: -10;
}
input[type="text"] {
  display: block;
  min-width: 90%;
  height: 35px;
  border-radius: 10px;
  outline: none;
  border: 1px solid grey;
  margin-top: 20px;
  padding-left: 20px;
  margin-bottom: 20px;
  color: #111;
}
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px;
  color: rgb(59, 39, 17);
  font-weight: 700;
  font-size: 20px;
}
.pagination span {
  margin-top: -4px;
}
.page-button {
  font-size: 30px;
  outline: none;
  border: none;
  background-color: rgba(0, 0, 0, 0);
  cursor: pointer;
  transition: 0.2s;
}
.page-button i {
  color: rgb(59, 39, 17);
  font-size: 40px;
}
.page-button:active {
  transform: translateY(2px);
}
#header {
  background-color: rgba(0, 0, 0, 0);
  width: 100%;
  display: inline-block;
  margin-left: 50px;
  margin-top: 2px;
}
.nav-buttons {
  display: flex;
}
.buttons {
  margin-right: 20px;
  background-color: rgb(59, 39, 17);
  color: #ffffff;
  text-transform: uppercase;
  outline: none;
  border: none;
  text-align: center;
  display: inline-block;
  padding: 10px 20px;
  border-radius: 10px;
  font-size: 15px;
  box-shadow: rgb(0 0 0 / 8%) 0px 4px;
  cursor: pointer;
  transition: 0.2s;
  font-weight: 600;
  letter-spacing: 1.5px;
}
.buttons:active {
  transform: translateY(2px);
}
#logo {
  margin: 0;
  font-size: 90px;
  display: inline-block;
  color: rgb(59, 39, 17);
  font-family: "Crimson Text", serif !important;
}
.search-gif {
  height: 100%;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
}
.header-box {
  display: flex;
  height: 300px;
  margin: 50px 40px;
  background-color: rgb(255, 215, 74);
  border-radius: 19px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
  padding-right: 40px;
}
.subtext {
  margin: 0;
  margin-left: 5px;
  margin-top: -7px;
  color: rgb(59, 39, 17);
  font-size: 20px;
  font-weight: 600;
}
@media screen and (max-width: 996px) {
  .search-gif {
    display: none;
  }
  #logo {
    margin: 0;
    font-size: 70px;
    text-align: center;
    display: block;
  }
  .header-box {
    display: flex;
    height: 245px;
    margin: 10px;
    margin-top: 30px;
    margin-bottom: 30px;
    padding: 10px;
  }
  #header {
    max-width: 100%;
    margin: 0;
    padding: 0;
    border-radius: 20px;
    align-items: center;
  }
  .buttons {
    margin-right: 0px;
    font-size: 13px;
  }
  .nav-buttons {
    width: 100%;
    display: flex;
    justify-content: space-around;
  }
  .subtext {
    font-size: 16px;
    text-align: center;
  }
  input[type="text"] {
    min-width: 80%;
    margin: auto;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  #char-box {
    margin-top: 0;
    padding-top: 0;
  }
}
@media screen and (max-width: 361px) {
  .nav-buttons {
    flex-wrap: wrap;
  }
  #logo {
    font-size: 60px;
  }
  .buttons {
    margin-bottom: 15px;
  }
  .header-box {
    height: 235px;
  }
}
@media screen and (max-width: 306px) {
  #logo {
    font-size: 50px;
  }
  input[type="text"] {
    margin: 10px;
  }
  .buttons {
    margin-bottom: 10px;
  }
  .header-box {
    height: 260px;
  }
}

.char-card {
  margin: 0 15px;
  margin-bottom: 50px;
  border-radius: 10px;
  width: 300px;
  height: 340px;
  position: relative;
  transform-style: preserve-3d;
  outline: none;
  border: none;
  padding: 0;
  background-color: rgba(0, 0, 0, 0);
  cursor: pointer;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.char-card .face {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transition: 1s ease-in-out;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
  transform: perspective(500px) rotateY(0deg);
}
.char-card .face.front {
  background-color: rgb(59, 39, 17);
  border-radius: 10px;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
.card-content {
  text-align: center;
  padding: 20px;
  font-weight: 600;
}
.char-card:active .face.front {
  transform: perspective(500px) rotateY(180deg);
}
.info-heading {
  font-size: 1.5rem;
  color: rgb(255, 215, 74);
  font-weight: 700;
}
.char-card .face.back {
  background-color: rgb(59, 39, 17);
  transform: perspective(500px) rotateY(180deg);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
}
.char-card:active .face.back {
  transform: perspective(500px) rotateY(360deg);
}
@media (hover: hover) {
  .char-card:hover .face.back {
    transform: perspective(500px) rotateY(360deg);
  }
  .char-card:hover .face.front {
    transform: perspective(500px) rotateY(180deg);
  }
}
.character-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.name-div {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.name-div h3 {
  color: rgb(255, 215, 74);
  margin: 0;
  z-index: 10;
  padding: 0 10px;
  position: absolute;
  top: 307px;
  text-align: center;
  font-size: 16px;
}
@media screen and (min-width: 480px) {
  .char-card {
    width: 270px;
    height: 315px;
  }
  .name-div h3 {
    top: 281px;
  }
}
@media screen and (max-width: 369px) {
  .char-card {
    width: 210px;
    height: 240px;
    margin-bottom: 30px;
  }
  .name-div h3 {
    top: 214px;
  }
  .info-heading {
    font-size: 16px;
  }
}


.eps-card {
  background-color: rgb(59, 39, 17);
  margin-bottom: 40px;
  color: white;
  border-radius: 10px;
  width: 370px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
}
.eps-img {
  width: 100%;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.eps-info {
  padding: 0 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  flex-direction: column;
  margin-top: 7px;
  margin-bottom: 20px;
}
.eps-name {
  color: rgb(255, 215, 74);
  font-weight: 600;
  font-size: 1.25rem;
}
.eps-date {
  margin-top: 5px;
}

